<template>
  <div id="edm-display-component">
    <div class="main-container">
      <div class="top-container">
        <div class="title-container">
          <h1 class="title">{{ subject }}</h1>
          <div class="input-item">
            <label class="label">Send email to: </label>
            <el-input v-model="email" placeholder="e-mail"></el-input>
          </div>
        </div>

        <div class="buttons-container">
          <el-button
            v-permission="'edm_update'"
            type="success"
            @click="onEditClick"
            >Edit</el-button
          >
          <el-button type="success" @click="onSendClick">Send</el-button>
        </div>
      </div>

      <div class="text-container">
        <p class="text">
          {{ messageBody }}
        </p>
      </div>

      <div class="cover-container">
        <div class="brand-name-container">
          {{ brandName }}
        </div>
        <div v-if="coverPhoto" class="template-container">
          <img
            class="edm-cover-image"
            :src="formatImageUrl(coverPhoto.file)"
            alt="cover"
          />
        </div>
        <div class="message-container">
          {{ message }}
        </div>
      </div>

      <div class="fabrics-container" v-if="collections.length">
        <el-row>
          <el-col v-for="(item, index) in collections" :key="index" :span="12">
            <div class="card">
              <div
                class="image"
                :style="{
                  backgroundImage: `url('${formatImageUrl(
                    item.fabric.image_url
                  ) + '@150-150'}')`
                }"
              ></div>
              <div class="right-container">
                <h3>{{ item.fabric.name }}</h3>
                <p>{{ item.fabric.real_composition }}</p>
              </div>
            </div>
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, toRefs, computed, onMounted } from "vue";
import { useStore } from "vuex";

import { formatImageUrl, loading } from "@/utils";

import { GetCollection, GetEdm, PostSendEdm } from "@/typings/store";
import { EdmCover, EdmParams, EdmResponse, SendEdmParams } from "@/typings/edm";
import { ElNotification } from "element-plus";
import { useRouter } from "vue-router";
import { getCover } from "@/constants/edm-cover";
import { postUpdateEdm } from "@/apis/edm";

const Component = defineComponent({
  name: "EDMDisplay",
  props: {
    id: {
      type: Number,
      default: 0
    }
  },
  setup(props) {
    const store = useStore();
    const router = useRouter();

    const { id } = toRefs(props);

    const email = ref("");
    const subject = ref("");
    const brandName = ref("");
    const message = ref("");
    const messageBody = ref("");
    const collectionId = ref(-1);
    const collections = ref([]);
    const coverPhoto = ref<EdmCover | null>(null);

    const detail = computed(() => store.state.edm.detail);
    const tempCollections = computed(() => store.state.collection.detail);

    const getCollectionId = (detail: EdmResponse) => {
      if (!detail) return null;
      return detail.chosen_collection;
    };

    const fetchCollection = async (id: number) => {
      await store.dispatch(GetCollection, id);
      collections.value = tempCollections.value.collection_fabrics;
    };

    const init = async () => {
      const loadingInstance = loading();
      try {
        await store.dispatch(GetEdm, id.value);
        subject.value = detail.value.subject;
        brandName.value = detail.value.recipient_brand;
        message.value = detail.value.message;
        messageBody.value = detail.value.message_body;
        coverPhoto.value = detail.value.cover_photo;
        collectionId.value = detail.value.chosen_collection;

        if (collectionId.value) {
          await fetchCollection(collectionId.value);
        }
      } finally {
        loadingInstance.close();
      }
    };

    onMounted(async () => await init());

    const onEditClick = async () => {
      // *** Check permission
      const params: EdmParams = {
        subject: detail.value.subject,
        message: detail.value.message,
        message_body: detail.value.messageBody,
        html_message: detail.value.html_message,
        chosen_collection: collectionId.value
      };
      if (coverPhoto.value) {
        params.cover_photo = coverPhoto.value.id;
      }
      await postUpdateEdm(id.value, params);
      // ***

      await router.push({ name: "EDMDetail", params: { id: id.value } });
    };

    const onSendClick = async () => {
      if (!email.value) {
        return ElNotification({
          title: "Warning",
          type: "warning",
          message: `You should enter email.`
        });
      }

      const params: SendEdmParams = {
        recipient_list: email.value.split(",").map(it => it.trim()),
        mail: id.value,
        sender: 1
      };

      try {
        await store.dispatch(PostSendEdm, params);
        ElNotification({
          title: "Success",
          type: "success",
          message: `Send e-mail successful.`
        });
      } finally {
        email.value = "";
      }
    };

    return {
      email,
      subject,
      brandName,
      message,
      messageBody,
      coverPhoto,
      collections,
      collectionId,
      detail,

      onEditClick,
      onSendClick,

      getCover,
      formatImageUrl
    };
  }
});

export default Component;
</script>

